
 .li_nav  , a , button {
    font-weight: 500;
    font-size: 16px;
    color: rgb(255, 255, 255);
    text-decoration: none;
}

.header_inNav_page {
    background-color: rgba(0,0,0,.5);
    padding : 10px 10%;
}

.ul_nav { 
    list-style: none;
    display: flex;
}

.ul_nav  .li_nav  {
    display: inline-block;
    padding:0px 20px;
}

.ul_nav  .li_nav  a {
    transition : all 0.3s ease 0s ; 
    color:white;
    }

    .ul_nav  .li_nav  a:hover {
        color:#0088a9 ;
        border-bottom: rgba(255, 255, 255, 0.8) 3px solid;
        padding-bottom: 15%;
    }

    .ul_nav  {
        display: flex;
        justify-content: space-between;
    }

    .li_dash {
list-style: none;
text-align: center;
margin-bottom: 3px;
color:black;
font-size: 30px;
justify-content: space-between;

    }

    .col-md-4{
        max-width: 30%;
    }

    .box {
        margin:10px 0px;

        background-color: white;
    }

    
